import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

let one = {
  number: "1",
  title: "Able",
  subTitle: "     "
}
let two = {
  number: "2",
  title: "",
  text: ["1. ", "2.  ", "3.  ", "4.  "],
}
let three = {
  number: "3",
  title: "1",
  image: {},
  text: " "
}
let four = {
  number: "4",
  title: "2",
  text: " 2"
}
let five = {
  number: "5",
  title: "Thanks for listening",
  text: ""
}

export default new Vuex.Store({
  state: {
    code: "",
    router: "down",
    ppt: {
      _id : "abc",
      template: 1,
      now: 0,
      number: []
    }

  },
  mutations: {
    addview1: function (state) {
      state.ppt.number.push({ number: "1", title: " ", subTitle: " " })
    },
    addview2: function (state) {
      state.ppt.number.push({
        number: "2", title: "",
        text: ["1. 1", "2. 2", "3. 3", "4. 4"],
      })
    },
    addview3: function (state) {
      state.ppt.number.push({ number: "3", title: " ", image: "", text: " " })
    },
    addview4: function (state) {
      state.ppt.number.push({ number: "4", title: " ", text: " " })
    },
    addview5: function (state) {
      state.ppt.number.push({ number: "5", title: "Thanks for listening", text: "" })
    },
    move(){
      router.push("/mainview");
    }
  },
  actions: {
    make(data){
      return new Promise(()=>{ axios.post(`http://192.168.35.84:4000/certification/make`, 
          { ppt:data.state.ppt})
          .then(response => {
           console.log("",response.data.ppt)
           this.state.ppt = response.data.ppt
          }).catch((ex) => {
             console.log(" : ",ex)
          })
      })
  },
  save(data){
    return new Promise(()=>{ axios.post(`http://192.168.35.84:4000/certification/save`, 
        { ppt:data.state.ppt })
        .then(response => {
         console.log("",response.data.ppt)
         this.state.ppt = response.data.ppt
        }).catch((ex) => {
          console.log(" : ",ex)
        })
      })
    },
    check(data){
      return new Promise((resolve, reject)=>{ axios.post(`http://192.168.35.84:4000/certification/check`, 
      { code:data.state.code })
      .then(response => {
        console.log("",response.data)
        this.state.ppt = response.data
        if(response.data){
        resolve({st : true});
        }
        else {
          resolve({st : false});
        }
      }).catch((ex) => {
         console.log(" : ",ex)
         reject({st : false});
      })
  })
}

  


  },
  modules: {
  }
})
